body {
  font-family: "Quicksand", sans-serif;
  display: flex;
  justify-content: center;
  padding: 0;
  color: #4d4d4d;
}
#title {
  text-align: center;
}
#dino {
  border-collapse: collapse;
  border: 3px solid #ddd;
}
#dino td,
#dino th {
  border: 1px solid #ddd;
  padding: 12px;
}
#dino tr:hover {
  background-color: #ddd;
}
#dino th {
  padding: 10px;
  text-align: center;
  background-color: #89cff0;
  color: white;
}
.remove {
  text-align: center;
}
.button {
  border: none;
  outline: none;
  font-size: 11px;
  font-family: "Quicksand", sans-serif;
  color: #f44336;
  padding: 3px 10px;
  border-radius: 8px;
  cursor: pointer;
  border: 1px solid #f44336;
  background-color: transparent;
}
.button:active {
  border: 1px solid blue;
}
